.movie__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 30px;
    max-width: 1024px;
  }
  
  .movie__card {
    overflow: hidden;
    box-shadow: 0px 2px 4px 0px #000;
    border-radius: 10px;
    position: relative;
    max-width: 300px;
    margin-bottom: 30px;
    transition: transform 0.5s;
  }
  .movie__img {
    width: 100%;
    border-bottom: 1px solid #000;
    height: 450px;
    object-fit: cover;
  }
  .movie__overlay {
    width: 100%;
    height: 100px;
    padding: 5px 15px;
    text-decoration: none;
    font-weight: 400;
  }
  .home-slogan {
    color: var(--tertiary-color);
    margin-top: 70px;
    margin-bottom: 60px;
  }
  
  .movie__card:hover {
    transform: scale(1.1);
  }

  @media screen and (min-width: 1024px) {
    .movie__list {
      justify-content: flex-start;
      padding-left: 16px;
    }
  }